import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import './style.css';

const Search = ({ showSearch, setShowSearch, setCurrentPage }) => {
  const [searchQuery, setSearchQuery] = useState('');
  const searchPage = useNavigate();

  const sendSearchQuery = (e) => {
    e.preventDefault();
    if (searchQuery === '') {
      alert('Please enter a search query');
    } else {
      // Backend pe query bhej (assume /search page fetch karega api/movies?search=query)
      searchPage(`/search/${searchQuery}`);
      setShowSearch(false);
      setCurrentPage(1);
      setSearchQuery('');
    }
  };

  const hideShowSearch = (e) => {
    e.preventDefault();
    if(e.target.className === 'search-container show') {
      setShowSearch(false);
    }
  }

  useEffect(() => {
    if(showSearch) {
      document.body.style = 'overflow: hidden';
    }
    return () => {
      document.body.style = 'overflow: auto';
    }
  }, [showSearch]);

  return (
    <div className={showSearch ? 'search-container show' : 'search-container'} onClick={hideShowSearch}>
      <form>
        <input
          type='text'
          placeholder='Search'
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          autoFocus
        />
        <button className='link btn search-btn' onClick={sendSearchQuery}>
          <i className='ri-search-line'></i>
        </button>
      </form>
    </div>
  );
};

export default Search;